/* =====  ===== */
.home-container {
    flex: 1;
    overflow-y: auto;
    padding: 2rem;
    margin-left: 200px;
}

/*  */
.home-search-section {
    max-width: 800px;
    margin: 0 auto 3rem;
    position: relative;
}

.search-bar-home {
    position: relative;
    display: flex;
    align-items: center;
    background: rgba(30, 41, 59, 0.8);
    border-radius: 12px;
    padding: 1rem 1.5rem;
    border: 2px solid rgba(168, 85, 247, 0.3);
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.3);
}

.search-bar-home:focus-within {
    border-color: rgba(168, 85, 247, 0.6);
    box-shadow: 0 4px 20px rgba(168, 85, 247, 0.2);
}

.search-icon {
    color: #a855f7;
    margin-right: 1rem;
}

.search-input-home {
    flex: 1;
    background: transparent;
    border: none;
    outline: none;
    color: #e9d5ff;
    font-size: 1.1rem;
}

.search-input-home::placeholder {
    color: #94a3b8;
}

.search-clear-btn {
    background: rgba(168, 85, 247, 0.2);
    border: none;
    border-radius: 50%;
    width: 32px;
    height: 32px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    color: #a855f7;
    transition: all 0.2s ease;
}

.search-clear-btn:hover {
    background: rgba(168, 85, 247, 0.4);
    transform: scale(1.1);
}

/*  */
.search-results-home {
    position: absolute;
    top: calc(100% + 0.5rem);
    left: 0;
    right: 0;
    background: rgba(30, 41, 59, 0.98);
    border-radius: 12px;
    padding: 1rem;
    max-height: 500px;
    overflow-y: auto;
    border: 1px solid rgba(168, 85, 247, 0.3);
    box-shadow: 0 10px 40px rgba(0, 0, 0, 0.5);
    z-index: 100;
}

.search-loading,
.search-empty {
    text-align: center;
    padding: 2rem;
    color: #94a3b8;
    font-style: italic;
}

.results-list {
    display: flex;
    flex-direction: column;
    gap: 0.75rem;
}

.result-item {
    background: rgba(168, 85, 247, 0.05);
    border-left: 3px solid #a855f7;
    border-radius: 8px;
    padding: 1rem;
    transition: all 0.2s ease;
    cursor: pointer;
}

.result-item:hover {
    background: rgba(168, 85, 247, 0.15);
    transform: translateX(4px);
}

.result-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 0.5rem;
}

.result-header h4 {
    color: #e9d5ff;
    font-size: 1.1rem;
    margin: 0;
}

.result-knowledge {
    background: rgba(251, 191, 36, 0.2);
    color: #fbbf24;
    padding: 0.25rem 0.75rem;
    border-radius: 12px;
    font-size: 0.85rem;
    font-weight: 600;
}

.result-desc {
    color: #c4b5fd;
    margin: 0.5rem 0;
    line-height: 1.5;
}

.result-meta {
    display: flex;
    gap: 1rem;
    font-size: 0.85rem;
    color: #94a3b8;
}

/*  */
.node-card {
    background: rgba(30, 41, 59, 0.8);
    border-radius: 12px;
    padding: 1.5rem;
    border: 2px solid rgba(168, 85, 247, 0.2);
    transition: all 0.3s ease;
    cursor: pointer;
}

.node-card:hover {
    transform: translateY(-4px);
    border-color: rgba(168, 85, 247, 0.5);
    box-shadow: 0 8px 24px rgba(168, 85, 247, 0.3);
}

.node-card-header {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    margin-bottom: 1rem;
}

.node-card-header h3 {
    color: #e9d5ff;
    font-size: 1.25rem;
    margin: 0;
    flex: 1;
}

.node-badge {
    padding: 0.25rem 0.75rem;
    border-radius: 12px;
    font-size: 0.75rem;
    font-weight: 600;
    text-transform: uppercase;
    letter-spacing: 0.5px;
}

.root-badge {
    background: rgba(16, 185, 129, 0.2);
    color: #10b981;
    border: 1px solid rgba(16, 185, 129, 0.3);
}

.featured-badge {
    background: rgba(245, 158, 11, 0.2);
    color: #f59e0b;
    border: 1px solid rgba(245, 158, 11, 0.3);
}

.node-card-desc {
    color: #c4b5fd;
    line-height: 1.6;
    margin-bottom: 1rem;
}

.node-card-footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 1rem;
    border-top: 1px solid rgba(168, 85, 247, 0.2);
}

.node-stat {
    display: flex;
    align-items: center;
    gap: 0.5rem;
    color: #94a3b8;
    font-size: 0.9rem;
}

.node-stat svg {
    color: #fbbf24;
}

/*  */
.root-nodes-section {
    margin-bottom: 3rem;
}

.section-title {
    color: #e9d5ff;
    font-size: 1.75rem;
    font-weight: 700;
    margin-bottom: 1.5rem;
    text-align: center;
    text-transform: uppercase;
    letter-spacing: 1px;
    background: linear-gradient(135deg, #a855f7 0%, #ec4899 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
}

.root-nodes-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    gap: 1.5rem;
    justify-items: center;
}

.root-node-card {
    width: 100%;
    max-width: 400px;
}

/*  */
.featured-nodes-section {
    margin-bottom: 2rem;
}

.featured-nodes-scroll {
    display: flex;
    gap: 1.5rem;
    overflow-x: auto;
    padding: 1rem 0.5rem;
    scroll-behavior: smooth;
}

.featured-nodes-scroll::-webkit-scrollbar {
    height: 8px;
}

.featured-nodes-scroll::-webkit-scrollbar-track {
    background: rgba(30, 41, 59, 0.5);
    border-radius: 4px;
}

.featured-nodes-scroll::-webkit-scrollbar-thumb {
    background: rgba(168, 85, 247, 0.5);
    border-radius: 4px;
}

.featured-nodes-scroll::-webkit-scrollbar-thumb:hover {
    background: rgba(168, 85, 247, 0.7);
}

.featured-node-card {
    min-width: 320px;
    max-width: 320px;
    flex-shrink: 0;
}

.empty-message {
    text-align: center;
    padding: 3rem;
    color: #94a3b8;
    font-style: italic;
    font-size: 1.1rem;
}

/*  -  */
.featured-badge-small {
    background: rgba(245, 158, 11, 0.2);
    color: #f59e0b;
    padding: 0.25rem 0.75rem;
    border-radius: 12px;
    font-size: 0.75rem;
    font-weight: 600;
    border: 1px solid rgba(245, 158, 11, 0.3);
}

.btn-featured {
    background: linear-gradient(135deg, #f59e0b 0%, #d97706 100%);
    color: white;
}

.btn-featured:hover {
    background: linear-gradient(135deg, #d97706 0%, #b45309 100%);
    transform: translateY(-2px);
}

.btn-featured-active {
    background: linear-gradient(135deg, #64748b 0%, #475569 100%);
    color: white;
}

.btn-featured-active:hover {
    background: linear-gradient(135deg, #475569 0%, #334155 100%);
    transform: translateY(-2px);
}

/* =====  ===== */
.node-detail-container {
    min-height: calc(100vh - 120px);
    padding: 2rem;
    display: flex;
    flex-direction: column;
    margin-left: 200px;
}

/* =====  ===== */
.navigation-sidebar {
    position: fixed;
    left: 1rem;
    top: 140px;
    width: 180px;
    background: rgba(30, 41, 59, 0.95);
    border-radius: 12px;
    border: 2px solid rgba(168, 85, 247, 0.5);
    box-shadow: 0 10px 40px rgba(0, 0, 0, 0.5);
    padding: 1rem;
    max-height: calc(100vh - 160px);
    overflow-y: auto;
    z-index: 100;
    animation: slideInLeft 0.3s ease-out;
}

/*  */
.navigation-header {
    margin-bottom: 1rem;
}

.navigation-title {
    font-size: 0.9rem;
    font-weight: 600;
    color: #a855f7;
    margin: 0 0 0.5rem 0;
    text-align: center;
    text-transform: uppercase;
    letter-spacing: 0.5px;
}

.navigation-divider {
    height: 2px;
    background: linear-gradient(90deg, transparent, rgba(168, 85, 247, 0.5), transparent);
    margin-bottom: 0.75rem;
}

.nav-item {
    background: rgba(51, 65, 85, 0.6);
    border-radius: 8px;
    padding: 0.75rem;
    margin-bottom: 0.5rem;
    transition: all 0.3s ease;
    border: 2px solid transparent;
}

.nav-item.clickable {
    cursor: pointer;
}

.nav-item.clickable:hover {
    background: rgba(168, 85, 247, 0.2);
    border-color: rgba(168, 85, 247, 0.5);
    transform: translateX(4px);
}

/*  */
.nav-item.omit-item {
    background: linear-gradient(135deg, rgba(251, 191, 36, 0.2) 0%, rgba(245, 158, 11, 0.2) 100%);
    border-color: rgba(251, 191, 36, 0.5);
}

.nav-item.omit-item:hover {
    background: linear-gradient(135deg, rgba(251, 191, 36, 0.3) 0%, rgba(245, 158, 11, 0.3) 100%);
    border-color: rgba(251, 191, 36, 0.7);
    transform: translateX(4px);
}

.nav-item.omit-item .nav-label {
    color: #fbbf24;
    font-weight: 600;
}

.nav-item.active {
    background: rgba(168, 85, 247, 0.3);
    border-color: #a855f7;
    box-shadow: 0 0 15px rgba(168, 85, 247, 0.4);
}

.nav-label {
    color: #e9d5ff;
    font-size: 0.9rem;
    font-weight: 500;
    display: block;
    word-break: break-word;
    line-height: 1.4;
}

.nav-arrow {
    text-align: center;
    color: #a855f7;
    font-size: 1.2rem;
    margin: 0.25rem 0;
    font-weight: bold;
}

/*  */
.nav-branch {
    display: flex;
    gap: 0.5rem;
    margin-bottom: 0.5rem;
}

.nav-item.branch-item {
    flex: 1;
    padding: 0.5rem;
    font-size: 0.85rem;
    margin-bottom: 0;
}

.nav-item.branch-item .nav-label {
    font-size: 0.8rem;
}

.navigation-sidebar::-webkit-scrollbar {
    width: 6px;
}

.navigation-sidebar::-webkit-scrollbar-track {
    background: rgba(30, 41, 59, 0.5);
    border-radius: 3px;
}

.navigation-sidebar::-webkit-scrollbar-thumb {
    background: rgba(168, 85, 247, 0.5);
    border-radius: 3px;
}

.navigation-sidebar::-webkit-scrollbar-thumb:hover {
    background: rgba(168, 85, 247, 0.7);
}

.node-detail-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 2rem;
    background: transparent;
    padding: 1.5rem;
    border-radius: 12px;
    border: none;
}

.detail-title {
    color: #e9d5ff;
    font-size: 2rem;
    font-weight: 700;
    margin: 0;
}

.node-detail-canvas {
    background: transparent;
    border: none;
    cursor: pointer;
}

/*  */
.node-info-modal {
    max-width: 700px;
    max-height: 85vh;
    overflow-y: auto;
}

.node-info-section {
    margin-bottom: 2rem;
}

.info-section-title {
    color: #e9d5ff;
    font-size: 1.75rem;
    font-weight: 700;
    margin-bottom: 1rem;
    text-align: center;
    background: linear-gradient(135deg, #a855f7 0%, #ec4899 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
}

.info-section-desc {
    color: #c4b5fd;
    font-size: 1.05rem;
    line-height: 1.7;
    text-align: center;
    padding: 0 1rem;
}

.info-section-subtitle {
    color: #e9d5ff;
    font-size: 1.2rem;
    font-weight: 600;
    margin-bottom: 1rem;
    padding-bottom: 0.5rem;
    border-bottom: 2px solid rgba(168, 85, 247, 0.3);
}

.node-info-grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 1.5rem;
    margin-bottom: 2rem;
}

.info-item {
    background: rgba(168, 85, 247, 0.05);
    padding: 1rem;
    border-radius: 8px;
    border-left: 3px solid #a855f7;
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
}

.info-label {
    color: #94a3b8;
    font-size: 0.85rem;
    font-weight: 600;
    text-transform: uppercase;
    letter-spacing: 0.5px;
}

/* Location Resident Sidebar */
.location-resident-sidebar {
    position: fixed;
    right: 1rem;
    top: 180px;
    width: 250px;
    background: rgba(30, 41, 59, 0.95);
    border-radius: 12px;
    border: 2px solid rgba(168, 85, 247, 0.5);
    box-shadow: 0 10px 40px rgba(0, 0, 0, 0.5);
    padding: 1rem;
    max-height: calc(100vh - 160px);
    overflow-y: auto;
    z-index: 100;
    animation: slideInRight 0.3s ease-out;
}

@keyframes slideInRight {
    from {
        opacity: 0;
        transform: translateX(20px);
    }
    to {
        opacity: 1;
        transform: translateX(0);
    }
}

.location-sidebar-header {
    border-bottom: 2px solid rgba(168, 85, 247, 0.3);
    padding-bottom: 0.75rem;
    margin-bottom: 1rem;
}

.location-sidebar-header h3 {
    color: #a855f7;
    font-size: 1rem;
    font-weight: 600;
    margin: 0;
    text-align: center;
    text-transform: uppercase;
    letter-spacing: 0.5px;
}

.location-node-title {
    color: #e9d5ff;
    font-size: 1.25rem;
    font-weight: 700;
    margin-bottom: 1rem;
    text-align: center;
}

.location-node-section {
    margin-bottom: 1.25rem;
}

.location-sidebar-jumpable {
    cursor: pointer;
    transition: all 0.2s ease;
}

.location-sidebar-jumpable:hover {
    transform: translateY(-2px);
    filter: brightness(1.06);
}

.section-label {
    color: #94a3b8;
    font-size: 0.75rem;
    font-weight: 600;
    margin-bottom: 0.5rem;
    text-transform: uppercase;
}

.section-content {
    color: #cbd5e1;
    font-size: 0.9rem;
    line-height: 1.5;
    background: rgba(51, 65, 85, 0.4);
    padding: 0.75rem;
    border-radius: 6px;
}

.section-tags {
    display: flex;
    flex-wrap: wrap;
    gap: 0.5rem;
}

.node-tag {
    padding: 0.25rem 0.5rem;
    border-radius: 4px;
    font-size: 0.75rem;
    font-weight: 500;
}

.parent-tag {
    background: rgba(16, 185, 129, 0.2);
    color: #10b981;
    border: 1px solid rgba(16, 185, 129, 0.3);
}

.child-tag {
    background: rgba(251, 191, 36, 0.2);
    color: #fbbf24;
    border: 1px solid rgba(251, 191, 36, 0.3);
}

.knowledge-content {
    max-height: 150px;
    overflow-y: auto;
    font-size: 0.85rem;
}

.location-sidebar-empty {
    text-align: center;
    color: #94a3b8;
    font-style: italic;
    padding: 2rem 0;
}

.admin-sidebar {
    border-color: rgba(239, 68, 68, 0.5);
}

.admin-sidebar .location-sidebar-header {
    border-bottom-color: rgba(239, 68, 68, 0.3);
}

.admin-sidebar .location-sidebar-header h3 {
    color: #ef4444;
}

.travel-sidebar-content {
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.travel-main-info {
    background: rgba(51, 65, 85, 0.45);
    border: 1px solid rgba(59, 130, 246, 0.35);
    border-radius: 8px;
    padding: 0.75rem;
}

.travel-destination {
    color: #dbeafe;
    font-size: 0.9rem;
    margin-bottom: 0.5rem;
}

.travel-metrics {
    display: flex;
    flex-direction: column;
    gap: 0.35rem;
    color: #bfdbfe;
    font-size: 0.8rem;
}

.travel-anim-layout {
    display: grid;
    grid-template-columns: 1fr;
    gap: 0.55rem;
}

.travel-node-card {
    background: rgba(30, 41, 59, 0.75);
    border: 1px solid rgba(148, 163, 184, 0.25);
    border-radius: 8px;
    padding: 0.65rem;
}

.travel-node-card.next {
    border-color: rgba(34, 197, 94, 0.4);
}

.travel-node-card.reached {
    border-color: rgba(59, 130, 246, 0.4);
}

.travel-node-label {
    color: #94a3b8;
    font-size: 0.72rem;
}

.travel-node-name {
    color: #e2e8f0;
    margin-top: 0.2rem;
    font-weight: 600;
    line-height: 1.25;
}

.travel-track-wrap {
    padding: 0.35rem 0;
}

.travel-track {
    height: 8px;
    border-radius: 999px;
    background: linear-gradient(90deg, rgba(34, 197, 94, 0.25), rgba(59, 130, 246, 0.25));
    position: relative;
    overflow: hidden;
}

.travel-progress-dot {
    position: absolute;
    top: 50%;
    width: 14px;
    height: 14px;
    border-radius: 50%;
    transform: translate(-50%, -50%);
    background: radial-gradient(circle at 30% 30%, #ffffff, #f59e0b 70%);
    box-shadow: 0 0 12px rgba(245, 158, 11, 0.9);
    animation: travelPulse 1s ease-in-out infinite;
}

.travel-stop-btn {
    width: 100%;
    justify-content: center;
}

@keyframes travelPulse {
    0%, 100% {
        transform: translate(-50%, -50%) scale(1);
    }
    50% {
        transform: translate(-50%, -50%) scale(1.1);
    }
}

/* WebGL container in Home */
.webgl-scene-container {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    z-index: 0;
}

.webgl-canvas {
    display: block;
    width: 100%;
    height: 100%;
}

/* Search Container in Home */
.search-container {
    position: absolute;
    top: 120px;
    left: 50%;
    transform: translateX(-50%);
    width: 600px;
    max-width: 90%;
    z-index: 50;
}

.floating-search-bar {
    background: rgba(30, 41, 59, 0.9);
    border-radius: 12px;
    padding: 0.5rem;
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.5);
    border: 1px solid rgba(168, 85, 247, 0.3);
}

.search-and-create-container {
    display: flex;
    gap: 0.5rem;
}

.search-input-wrapper {
    flex: 1;
    display: flex;
    align-items: center;
    background: rgba(15, 23, 42, 0.6);
    border-radius: 8px;
    padding: 0.5rem 1rem;
    border: 1px solid transparent;
    transition: all 0.3s ease;
}

.search-input-wrapper:focus-within {
    border-color: #a855f7;
    background: rgba(15, 23, 42, 0.8);
}

.search-input-floating {
    flex: 1;
    background: transparent;
    border: none;
    color: white;
    font-size: 1rem;
    margin-left: 0.5rem;
    outline: none;
}

.create-node-btn {
    white-space: nowrap;
}

.search-results-panel {
    margin-top: 0.5rem;
    background: rgba(30, 41, 59, 0.95);
    border-radius: 12px;
    border: 1px solid rgba(168, 85, 247, 0.3);
    max-height: 400px;
    overflow: hidden;
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.5);
    animation: slideDown 0.2s ease-out;
}

.search-results-scroll {
    overflow-y: auto;
    max-height: 400px;
    padding: 0.5rem;
}

.search-result-card {
    padding: 1rem;
    border-radius: 8px;
    background: rgba(51, 65, 85, 0.4);
    margin-bottom: 0.5rem;
    cursor: pointer;
    transition: all 0.2s ease;
    border: 1px solid transparent;
}

.search-result-card:last-child {
    margin-bottom: 0;
}

.search-result-card:hover {
    background: rgba(168, 85, 247, 0.2);
    border-color: rgba(168, 85, 247, 0.5);
}

.search-card-title {
    color: #e9d5ff;
    font-weight: 600;
    margin-bottom: 0.25rem;
}

.search-card-desc {
    color: #94a3b8;
    font-size: 0.85rem;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
}

.search-no-results,
.search-loading-indicator {
    margin-top: 0.5rem;
    background: rgba(30, 41, 59, 0.9);
    padding: 1rem;
    border-radius: 8px;
    text-align: center;
    color: #94a3b8;
    border: 1px solid rgba(168, 85, 247, 0.3);
}

.home-announcement-dock {
    position: fixed;
    right: 0;
    top: 300px;
    z-index: 119;
    display: flex;
    align-items: flex-start;
    pointer-events: none;
}

.home-announcement-dock-panel {
    pointer-events: auto;
    width: 0;
    max-height: calc(100vh - 180px);
    opacity: 0;
    transform: translateX(24px);
    overflow: hidden;
    border-radius: 12px 0 0 12px;
    border: 0 solid rgba(244, 63, 94, 0.45);
    border-right: none;
    background: rgba(30, 41, 59, 0.95);
    box-shadow: none;
    transition: width 0.25s ease, opacity 0.2s ease, transform 0.25s ease, border-width 0.2s ease, box-shadow 0.2s ease;
}

.home-announcement-dock-panel.expanded {
    width: min(360px, calc(100vw - 88px));
    opacity: 1;
    transform: translateX(0);
    border-width: 2px;
    box-shadow: -8px 10px 34px rgba(0, 0, 0, 0.45);
}

.home-announcement-dock-header {
    margin: 0;
    padding: 0.75rem 0.9rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 0.6rem;
    border-bottom: 1px solid rgba(148, 163, 184, 0.25);
}

.home-announcement-dock-header h3 {
