}

.home-announcement-dock-toggle {
    pointer-events: auto;
    position: relative;
    border: 2px solid rgba(244, 63, 94, 0.62);
    border-right: none;
    background: rgba(15, 23, 42, 0.94);
    color: #e2e8f0;
    border-radius: 12px 0 0 12px;
