    transition: background 0.2s ease, border-color 0.2s ease;
}

.home-announcement-dock-toggle:hover {
    border-color: rgba(251, 113, 133, 0.85);
    background: rgba(30, 41, 59, 0.98);
}

.home-announcement-dock-label {
