    box-shadow: 0 0 0 2px rgba(15, 23, 42, 0.9);
}

.home-location-dock {
    position: fixed;
    right: 0;
    top: 170px;
    z-index: 120;
    display: flex;
    align-items: flex-start;
    pointer-events: none;
}

.home-location-dock-panel {
    pointer-events: auto;
    width: 0;
    max-height: calc(100vh - 150px);
    opacity: 0;
    transform: translateX(24px);
    overflow: hidden;
    border-radius: 12px 0 0 12px;
    border: 0 solid rgba(168, 85, 247, 0.5);
    border-right: none;
    background: rgba(30, 41, 59, 0.95);
    box-shadow: none;
    transition: width 0.25s ease, opacity 0.2s ease, transform 0.25s ease, border-width 0.2s ease, box-shadow 0.2s ease;
}

.home-location-dock-panel.expanded {
    width: min(340px, calc(100vw - 88px));
    opacity: 1;
    transform: translateX(0);
    border-width: 2px;
    box-shadow: -8px 10px 34px rgba(0, 0, 0, 0.45);
}

