    color: #f8fafc;
}

.home-location-panel-body {
    max-height: calc(100vh - 220px);
    overflow-y: auto;
    padding: 0.85rem;
}

.home-location-panel-body .location-sidebar-content {
    padding: 0;
}

.home-location-panel-body .location-node-title {
    font-size: 1.1rem;
}

.home-location-dock-toggle {
    pointer-events: auto;
    border: 2px solid rgba(168, 85, 247, 0.65);
    border-right: none;
    background: rgba(15, 23, 42, 0.94);
    color: #e2e8f0;
    border-radius: 12px 0 0 12px;
    min-height: 108px;
    width: 46px;
    display: flex;
