}

.home-location-dock-toggle:hover {
    border-color: rgba(56, 189, 248, 0.72);
    background: rgba(30, 41, 59, 0.98);
}

.home-location-dock-label {
    writing-mode: vertical-rl;
    text-orientation: mixed;
    font-size: 0.72rem;
    letter-spacing: 1px;
}

@media (max-width: 768px) {
    .home-announcement-dock {
        top: auto;
        bottom: 218px;
    }

    .home-announcement-dock-panel.expanded {
        width: min(320px, calc(100vw - 76px));
    }

    .home-announcement-dock-body {
        max-height: min(40vh, 320px);
    }

    .home-location-dock {
        top: auto;
        bottom: 96px;
    }

    .home-location-dock-panel.expanded {
        width: min(320px, calc(100vw - 76px));
    }

    .home-location-panel-body {
        max-height: min(46vh, 380px);
    }
}
